body {
  font-family: 'Courier New', Menlo, Monaco, monospace, -apple-system, BlinkMacSystemFont,
  "Segoe UI", "Roboto", "Oxygen",  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", 
  "Helvetica Neue", sans-serif;

  /* height: 100%; */
  /* margin: 0; */
  color: #93A1A1 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  height: 100%;
  margin: 0;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#stack1 > tbody > tr > td {
  border-color: #61dafb;
  border-width: 2px;
  border-style: solid;
  font-size: 30px;
  color: yellow;
  padding: 10px;
}

#stack1, #stack2, #stack3 {
  margin-right: 20px;
  margin-top: 10px;
  border-collapse: collapse;
  float: left;
}

#stack2 > tbody > tr > td {
  border-color: #20ee0d;
  border-width: 2px;
  border-style: solid;
  font-size: 30px;
  color: yellow;
  padding: 10px;
}

#stack3 > tbody > tr > td {
  border-color: #20ee0d;
  border-width: 2px;
  border-style: solid;
  font-size: 30px;
  color: yellow;
  padding: 10px;
}

